.page-title-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    // background-color: rgb(207, 147, 91);
    border-top: 2px solid var(--mainText);
    border-bottom: 2px solid var(--mainText);
    text-align: center;
    // background-image: url("../../assets/wood6.2.jpg");
