.rates-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    //border-top: 1px solid black;
    // border-right: 1px solid black;
    // border-left: 1px solid black;
    margin: 4rem 1rem 4rem;
    color: var(--navFontColor);
    
    h3 {
        width: 100%;
        height: 100%;
        text-align: center;
        font-weight: 400;
        background-color: var(--dropdown); 
        // background-color: rgb(20, 57, 90);
        background-color: rgb(83, 77, 64);
        background-color: rgb(21,47,94);
        background-color: rgb(21,47,94);
        background-color: var(--navbar);
        padding: 1rem;
        border: 1px solid black;
    }

    .titles {
        width: 100%;
        text-align: center;
        display: flex;
        h4 {
            width: 50%;
            font-size: 1.1rem;
            font-weight: 600;
            background-color: var(--navbar);
            // background-color: rgb(50, 103, 126);
            background-color: rgb(65, 112, 173);
            background-color: rgb(94,135,187);
            background-color: rgb(201, 201, 201);
            color: rgb(0, 0, 0);
            padding: 1rem;
            border-bottom: 1px solid black;
            border-right: 1px solid black;

            &:first-child {
                border-left: 1px solid black;    
            }
        }
    }
}

.rates-container {
    width: 100%;
}

.rates {
    display: flex;
    justify-content: center;
    align-items: center;
    // width: 100%;
    text-align: center;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    background-color: rgb(255, 255, 255);
    

    p {
        width: 50%;
        color: black;
        padding: 1rem;
        font-size: 1.05rem;
        
        &:first-child {
            border-right: 1px solid black;
        }
    }
}

@media screen and (min-width: 768px){
    .rates-wrapper {
        margin: 4rem 3rem 4rem;
    }
}

@media screen and (min-width: 1024px){
    .rates-wrapper {
        margin: 4rem 10rem 4rem;
    }
}

@media screen and (min-width: 1440px){
    .rates-wrapper {
        margin: 4rem 20rem 4rem;
    }
}
