@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&family=Poppins:wght@300;400&display=swap);
:root{--navbar: rgb(38, 15, 2);--dropdown: rgb(141, 81, 12);--background: rgb(255, 255, 255);--navFontColor: rgb(255, 255, 255);--mainText: rgb(82, 32, 3)}*{margin:0;padding:0;box-sizing:border-box;text-decoration:none;list-style:none}body{background-color:rgb(255, 255, 255);background-color:var(--background);font-family:'Open Sans', sans-serif;font-family:'Poppins', sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

nav{background-color:#111;background-color:var(--navbar);padding:1rem 1rem;display:flex;justify-content:space-around;align-items:center;position:-webkit-sticky;position:sticky;top:0;width:100%;height:5rem}.logo{text-align:left;width:60%;font-size:1.2rem}.logo a{color:var(--navFontColor)}.logo a:hover{color:var(--dropdown)}.menu-icon{display:flex;flex-direction:column;cursor:pointer}.menu-icon i{background-color:var(--navFontColor);width:25px;height:2px;margin:2px;border-radius:2px;z-index:10;transition:all ease 0.5s}.menu-icon .open:nth-child(1){-webkit-transform:rotate(45deg) translateY(9px);transform:rotate(45deg) translateY(9px)}.menu-icon .open:nth-child(2){opacity:0}.menu-icon .open:nth-child(3){-webkit-transform:rotate(-45deg) translateY(-9px);transform:rotate(-45deg) translateY(-9px)}.menu-icon .close:nth-child(1){-webkit-transform:rotate(0) translateY(0);transform:rotate(0) translateY(0)}.menu-icon .close:nth-child(2){opacity:1}.menu-icon .close:nth-child(3){-webkit-transform:rotate(0) translateY(0);transform:rotate(0) translateY(0)}.links{display:none}.nav-links-closed{position:absolute;background-color:var(--navFontColor);top:5rem;width:100%;height:0vh;transition:all 0.3s ease;overflow:hidden}.nav-links-closed a{color:var(--mainText)}.nav-links-closed ul{display:flex;flex-direction:column;align-items:center;justify-content:center}.nav-links-closed ul li{margin:3rem 0;font-size:1.2rem;font-weight:500;opacity:0}.nav-links{display:flex;justify-content:center;align-items:center;position:absolute;background-color:var(--navFontColor);top:5rem;width:100%;height:calc(100vh - 10rem);transition:all .5s ease;box-shadow:0px 2px 4px var(--mainText)}.nav-links a{color:var(--mainText)}.nav-links ul{display:flex;flex-direction:column;align-items:center;justify-content:center}.nav-links ul li{text-align:center;width:150px;margin:0rem 0 0;padding:1.5rem 0;font-size:1.2rem;font-weight:600;opacity:1;transition:opacity .5s ease 0.1s;border-bottom:1.5px solid var(--mainText)}.nav-links ul li:last-child{border-bottom:0px solid var(--mainText)}@media screen and (min-width: 768px){nav{padding:1rem 1rem}.logo{font-size:1.4rem}.menu-icon{display:none}.nav-links-closed{display:none}.nav-links{display:none}.links{display:flex;width:80%}.links ul{width:100%;display:flex;justify-content:flex-end;align-items:center}.links ul li{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding-left:1rem;font-weight:400}.links a{color:var(--navFontColor)}.links a:hover{color:var(--dropdown)}}@media screen and (min-width: 1024px){nav{padding:1rem 2rem}.links ul{text-align:center}.links ul li{padding-left:2rem;font-weight:500;font-size:1.1rem}}

.img-container{background-image:url("/duplex-val-thorens/static/media/val thores3.e4ec9a8c.jpg");background-repeat:no-repeat;background-size:cover;background-position:left;width:100%;height:30rem}

.description{display:flex;flex-direction:column;justify-content:center;align-items:center}.description .main-text{font-size:1.1rem;margin:3rem 3rem;text-align:left;font-weight:400;color:var(--mainText)}.description .view{width:100%;height:auto}@media screen and (min-width: 1024px){.view-wrapper{width:100%;display:flex;justify-content:center;align-items:center}.view-wrapper .text-wrapper{display:flex;flex-direction:column;justify-content:center;align-items:center;width:50%}.view-wrapper .border-text{border-top:3px solid var(--dropdown);width:100px}.view-wrapper .main-text{width:100%;margin:0rem 0rem;padding:2rem 5rem;text-align:center;font-size:1.2rem;font-weight:400}.view-wrapper .view{width:50%;height:auto}.reverse{flex-direction:row-reverse}}

.page-title-wrapper{display:flex;flex-direction:column;align-items:center;justify-content:center;border-top:2px solid var(--mainText);border-bottom:2px solid var(--mainText);text-align:center}.page-title-wrapper .page-title{padding:3rem 2rem 1rem;color:var(--mainText);font-weight:600;font-size:1.8rem}.page-title-wrapper .page-title2{padding:0rem 2rem 3.5rem;font-size:1.2rem;font-weight:400;color:var(--mainText)}

.img-container-apartment{background-image:url(/duplex-val-thorens/static/media/buildingView.46cd7ef0.jpg);background-repeat:no-repeat;background-size:cover;background-position:top;width:100%;height:25rem}

.rates-wrapper{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:4rem 1rem 4rem;color:var(--navFontColor)}.rates-wrapper h3{width:100%;height:100%;text-align:center;font-weight:400;background-color:var(--dropdown);background-color:#534d40;background-color:#152f5e;background-color:#152f5e;background-color:var(--navbar);padding:1rem;border:1px solid black}.rates-wrapper .titles{width:100%;text-align:center;display:flex}.rates-wrapper .titles h4{width:50%;font-size:1.1rem;font-weight:600;background-color:var(--navbar);background-color:#4170ad;background-color:#5e87bb;background-color:#c9c9c9;color:#000;padding:1rem;border-bottom:1px solid black;border-right:1px solid black}.rates-wrapper .titles h4:first-child{border-left:1px solid black}.rates-container{width:100%}.rates{display:flex;justify-content:center;align-items:center;text-align:center;border-left:1px solid black;border-right:1px solid black;border-bottom:1px solid black;background-color:#fff}.rates p{width:50%;color:black;padding:1rem;font-size:1.05rem}.rates p:first-child{border-right:1px solid black}@media screen and (min-width: 768px){.rates-wrapper{margin:4rem 3rem 4rem}}@media screen and (min-width: 1024px){.rates-wrapper{margin:4rem 10rem 4rem}}@media screen and (min-width: 1440px){.rates-wrapper{margin:4rem 20rem 4rem}}

.img-container-rates{background-image:url(/duplex-val-thorens/static/media/ratesPage.20f7f0ce.jpg);background-repeat:no-repeat;background-size:cover;background-position:center;width:100%;height:25rem}

.img-container-maps{background-image:url(/duplex-val-thorens/static/media/mapPage3.447ddfbb.jpg);background-repeat:no-repeat;background-size:cover;background-position:center;width:100%;height:25rem}

.img-container-contacts{background-image:url(/duplex-val-thorens/static/media/contactPage.2549bd90.jpg);background-repeat:no-repeat;background-size:cover;background-position:top;width:100%;height:25rem}

.footer-wrapper{background-color:var(--navbar);padding:0 0}.footer-contacts{display:flex;justify-content:center;align-items:center;background-color:var(--navbar)}.footer-contacts ul{display:flex;flex-direction:column;padding:2rem 0rem 0}.footer-contacts ul li{display:flex;font-size:0.9rem;color:var(--navFontColor);font-weight:500;padding:0.5rem 2rem}.footer-contacts ul li p{padding-left:0.3rem}.footer-container{height:5rem;background-color:var(--navbar);display:flex;justify-content:center;align-items:center;color:var(--navFontColor);font-size:0.8rem;padding:0rem 0;font-weight:300}@media screen and (min-width: 1024px){.footer-contacts ul{display:flex;flex-direction:row;justify-content:center;align-items:center;padding:2rem 0rem 0}.footer-contacts ul li{display:flex;justify-content:center;align-items:center;font-size:1rem;padding:0.5rem 1rem}.footer-container{font-size:0.9rem}}

